.searchbar{margin:16px 0}
.searchbar__inner{display:flex;gap:8px}
.searchbar__input{flex:1;padding:10px;border:1px solid #cbd5e1;border-radius:6px;font-size:14px}
.searchbar__input--error{border-color:#dc2626;background:#fef2f2}
.searchbar__btn{background:#2563eb;color:#fff;border:none;padding:10px 16px;border-radius:6px;font-weight:500}
.searchbar__btn:focus-visible{outline:3px solid rgba(37,99,235,.45)}
.searchbar__label{position:absolute;left:-999px}
.searchbar__error{min-height:18px;font-size:12px;color:#dc2626;margin-top:4px}
@media (max-width:560px){
	.searchbar__inner{flex-direction:column}
	.searchbar__btn{width:100%}
}
