.btn{--btn-bg:#2563eb;--btn-color:#fff;display:inline-flex;align-items:center;gap:6px;font-size:14px;font-weight:500;background:var(--btn-bg);color:var(--btn-color);border:none;padding:8px 14px;border-radius:6px;cursor:pointer;transition:.18s background,.18s color,.18s border-color;position:relative;line-height:1.2;text-decoration:none}
.btn:focus-visible{outline:3px solid rgba(37,99,235,0.45);outline-offset:2px}
.btn:hover:not(.btn--disabled):not(.btn--loading){filter:brightness(1.1)}
.btn:active:not(.btn--disabled):not(.btn--loading){filter:brightness(.92)}
.btn--ghost{--btn-bg:transparent;--btn-color:#2563eb;border:1px solid #2563eb}
.btn--danger{--btn-bg:#dc2626;--btn-color:#fff}
.btn--link{--btn-bg:transparent;--btn-color:#2563eb;padding:0;border:none}
.btn--disabled{opacity:.55;cursor:not-allowed}
.btn__spinner{width:16px;height:16px;border-radius:50%;border:2px solid #fff;border-top-color:rgba(0,0,0,0.25);animation:spin .8s linear infinite}
@keyframes spin{to{transform:rotate(360deg)}}
.btn--loading .btn__content{opacity:.65}
