.card{display:flex;gap:12px;border:1px solid #e5e7eb;padding:12px;border-radius:10px;background:#fff;box-shadow:0 2px 4px rgba(0,0,0,.04);transition:.2s box-shadow}
.card:hover{box-shadow:0 4px 10px rgba(0,0,0,.08)}
.card__image{width:120px;height:80px;object-fit:cover;border-radius:8px;background:#f3f4f6}
.card__title{margin:0 0 6px 0;font-size:16px;line-height:1.3}
.card__title a{color:#111;text-decoration:none}
.card__title a:hover{text-decoration:underline}
.card__desc{margin:0;color:#374151;font-size:14px}
.card__meta{font-size:12px;color:#6b7280;margin-top:8px}
@media (max-width:680px){
	.card{flex-direction:column}
	.card__image{width:100%;height:160px}
}
